const mutations = {
    RANDOMIZE_LANGUAGES(state) {
        state.languages.forEach(language => {
            language.animation = state.animations[Math.floor(Math.random() * (4 - 0) + 0)];
        });
    },
    RANDOMIZE_TOOLS(state) {
        state.tools.forEach(tool => {
            tool.animation = state.animations[Math.floor(Math.random() * (4 - 0) + 0)];
        });
    },
    RANDOMIZE_WORK(state) {
        state.work.forEach(job => {
            job.animation = state.animations[Math.floor(Math.random() * (4 - 0) + 0)];
        });
    },
    RANDOMIZE_STUDIES(state) {
        state.studies.forEach(study => {
            study.animation = state.animations[Math.floor(Math.random() * (4 - 0) + 0)];
        });
    }
}
const actions = {
    randomizeLanguages({ commit }){
        commit('RANDOMIZE_LANGUAGES');
    },
    randomizeTools({ commit }) {
        commit('RANDOMIZE_TOOLS');
    },
    randomizeWork({ commit }) {
        commit('RANDOMIZE_WORK');
    },
    randomizeStudies({ commit }) {
        commit('RANDOMIZE_STUDIES');
    }
}
const getters = {
    languages: state => {
        return state.languages;
    },
    tools: state => {
        return state.tools;
    },
    work: state => {
        return state.work;
    },
    studies: state => {
        return state.studies;
    }
}

function getTimeFromStartDate(startDate, endDate = new Date()){
    let months = (endDate.getFullYear() - startDate.getFullYear()) * 12;
    months -= startDate.getMonth();
    months += endDate.getMonth();

    let years = Math.round((months / 12) * 10) / 10;

    return months >= 12 ? years + (years > 1 ? " years" : " year") : (months <= 0 ? 0 + " months" : months + " months");
}

export const store = new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})

